.movie-item {
    width: 200px;
    height: 300px;
    font-size: 0.6rem;
    color: white;
    background-color: #161a1d;
    transition: transform 0.5s;
  }
  
  .movie-item:hover{
    transform: scale(1.1);
  }
  
  .movie-poster {
    height: 90%;
    width: 100%;
  }
  
  .movie-poster img {
    height: 100%;
    width: 100%;
  }
  
  .movie-info {
    height: 10%;
    display: flex;
    justify-content: space-between;
    padding: 5px;
    align-items: center;
  }
  
